@import url(https://fonts.googleapis.com/css2?family=Baloo+2&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap);


/* All rights reserved */ /* Baloo font */ /* Ubuntu font */ /* Montserrat font */ /* Dancing Script */

/* Global Properties */

* {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}
html {
  height: 100%;
  scroll-behavior: smooth;
}
body {
  background-color: rgb(66 62 62);
  color: white;
  font-family: "Baloo 2", "cursive";
  height: 100%;
  overflow-x: hidden;
}

a {
  color: #000;
}

/* Navigation bar */

.nav-bar {
  font-family: "Ubuntu", Arial, Helvetica, sans-serif;
  background-color: black;
  padding: 19px;
  display: flex;
  box-shadow: 0 0 18px #ffd9005b;
  padding-right: 0;
  z-index: 999;
  top: 0;
  left: 0;
  right: 0;
  position: sticky;
}

.nav-bar .logo {
  width: 100%;
  display: flex;
  align-items: center;
}

.nav-bar .logo a {
  color: #ffd700;
  letter-spacing: 0.15em;
}

.nav-bar .navbar {
  display: flex;
  width: 100%;
}

.nav-bar .navbar ul {
  display: flex;
  margin-left: auto;
  list-style-type: none;
}

.nav-bar .navbar ul li {
  padding: 0px 16px;
  margin: 0px 18px;
  align-items: right;
}

.nav-bar .navbar ul li a {
  text-decoration: none;
  color: white;
  letter-spacing: 0.15em;
}

.navbar ul li {
  display: flex;
  justify-content: center;
  align-items: center;
}

.material-icons {
  margin: 0 0.3em;
}

/* Navigation hover effect */

.navbar ul li a::after {
  background: none repeat scroll 0 0 transparent;
  bottom: 0;
  content: "";
  display: block;
  height: 2px;
  left: 50%;
  position: relative;
  background: #ffe259;
  transition: width 0.3s ease 0s, left 0.3s ease 0s;
  width: 0;
}

.navbar ul li a:hover::after {
  width: 100%;
  left: 0;
}

/* Main container */

.main {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.main-left {
  background-color: rgb(36, 36, 36);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  z-index: 90;
}

.main-heading {
  font-family: "Dancing Script", "cursive";
  font-size: 4.5em;
  z-index: 15;
}

.main-right {
  z-index: 100;
  height: 100%;
}

.main-right img {
  height: 100% !important;
  z-index: 100;
  position: relative;
}

/* Learn More button */

.learn-more {
  z-index: 13;
}

.btn {
  background: #ffe259;
  text-decoration: none;
  padding: 0.5em 2em;
  font-size: 1em;
  cursor: pointer;
  box-shadow: inset 0 0 0 0 orange;
  transition: ease-out 0.4s;
  margin: 2em;
  border: none;
}
.btn:hover {
  box-shadow: inset 0 -100px 0 0 #fff;
}

.btn {
  color: #000;
}

/*For every heading */

.heading {
  margin: 1em 0;
}

.heading::after {
  background: none repeat scroll 0 0 transparent;
  bottom: 0;
  content: "";
  display: block;
  height: 3px;
  background: orange;
  width: 1.8em;
  border-radius: 50px;
}

/* Social section */

.links {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  width: 100%;
}

.links li {
  display: flex;
  justify-content: space-evenly;
}

.links li a {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  margin: 0 1em;
  transition: 0.3s ease-in;
}

.icon {
  margin: 0.3em;
}

.links li a:hover {
  color: #ffafbd;
}

#social {
  padding-bottom: 1em;
}

/* About section */

.section {
  position: relative;
  z-index: 100;
  background-color: rgb(66 62 62);
  width: 100%;
  margin: -0.5em auto;
  padding: 0.5em 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.about-head {
  font-size: 4em;
  margin: 0.5em;
}

.my-self {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.about-img {
  width: 30em;
}

.skills-img {
  background: linear-gradient(#ffafbd, #ffc3a0);
  border-radius: 70% 30% 30% 70% / 70% 70% 30% 30%;
  width: 26em;
  margin: 1em;
}

.speciality {
  font-size: 1.3em;
  margin: 0 2em;
}

/* Contact section */

.contact {
  padding-bottom: 2em;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}

/* RESPONSIVE NAVBAR */

.checkbtn {
  font-size: 2em;
  color: #fff;
  margin: 0.3em 1em;
  cursor: pointer;
  display: none;
}

#check {
  display: none;
}

footer {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #000;
  z-index: 200;
  padding: 2em;
  flex-direction: column;
}

.quick-links {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 2em 0 0 0em;
}

.quick-links ul li a {
  color: #fff;
}

.quick-links ul li a:hover {
  color: #ffafbd;
}

/* Animation on scrolling */

/* Fade in */

.fade-in {
  opacity: 0;
  transition: opacity 1s ease-in;
}

.fade-in.appear {
  opacity: 1;
}

/* Slide in */

.my,
.skills-img {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}

.my-img,
.speciality {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}

.my,
.my-img,
.skills-img,
.speciality {
  transition: -webkit-transform 0.6s ease-out;
  transition: transform 0.6s ease-out;
  transition: transform 0.6s ease-out, -webkit-transform 0.6s ease-out;
}

.my.appear,
.my-img.appear,
.speciality.appear,
.skills-img.appear {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

@media screen and (max-width: 1150px) {
  body {
    overflow-x: hidden;
  }

  /* MAIN CONTAINER  */
  .main-container {
    width: 100vw;
    overflow: hidden;
  }

  .main {
    display: block;
    height: 100%;
    width: 100%;
    position: relative;
    z-index: 1;
  }

  .main::before {
    content: "";
    background-image: url(/my-web/static/media/hero-image.9bef24be.png);
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0px;
    left: 0px;
    z-index: -1;
    opacity: 0.5;
    background-repeat: no-repeat;
    background-size: cover;
  }

  .main-left {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 32px;
    text-align: center;
    background: none;
  }

  .main-right {
    display: none;
  }

  .name {
    font-size: 1.3em;
  }
}

@media screen and (max-width: 900px) {
  body {
    overflow-x: hidden;
  }

  .section {
    overflow-x: hidden;
  }

  .navbar {
    z-index: 999;
  }

  .checkbtn {
    display: block;
    margin-left: auto;
  }

  .menu-list {
    position: fixed;
    top: -200%;
    left: 0%;
    background-color: rgb(36, 36, 36);
    color: #fff;
    height: 100vh;
    width: 100vw;
    transition: 500ms cubic-bezier(0.03, 0.95, 0.11, 1.09);
  }

  .menu-list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  #check:checked ~ ul {
    top: 0%;
    z-index: -1;
  }

  .nav-bar .navbar ul li {
    font-size: 1em;
    margin-bottom: 1em;
    padding: 1em;
  }

  .main-left {
    width: 100vw;
    margin: 0;
    padding: 0;
    background: none;
  }

  .main-heading {
    font-size: 2.5em;
  }

  .visit-links ul {
    flex-direction: column;
  }

  .visit-links li {
    margin: 1em;
  }

  .visit-head,
  .about-head {
    font-size: 1.6em;
  }

  .form-input {
    width: 80%;
    font-size: 0.7em;
    padding: 1em;
  }

  .my-self {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .about-img {
    width: 20em;
  }

  .name {
    font-size: 1.2em;
  }

  .about-head {
    font-size: 2.5em;
  }

  .my {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }

  .my-skill {
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-size: 1em;
  }

  .links {
    flex-wrap: wrap;
  }

  .links li a {
    margin: 0 0.5em;
  }
}

